import Vue from 'vue'
import VeeValidate,{Validator} from 'vee-validate'
import zh_CN from 'vee-validate/dist/locale/zh_CN'
import VueI18n from 'vue-i18n'
Vue.use(VueI18n)
const i18n = new VueI18n({
  locale: 'zh_CN',
})
const dictionary = {
   zh_CN: {
    messages: {
      email: () => '',
      required: ( field )=> "" + field
    },
    attributes:{
      email:'',
      password:'',
      password2:'',
      account: '',
      tel: '',
      verify:'',
      realname:'',
      idcard:''
    }
  }
}

Vue.use(VeeValidate, {
  i18n,
  i18nRootKey: 'validation',
  dictionary
})



